














import { Component, Vue, Prop } from 'vue-property-decorator';

@Component
export default class RoomDetailHeader extends Vue {
  @Prop({default: ''}) private label!: string;

  private goBack(): void {
    if (window.history.length <= 1) {
      this.$router.push({path: '/'});
    } else {
      this.$router.back();
    }
  }
}
